<section class="login"><div class="container"><div class="login-wrap"><form class="login__form" action="#"><div class="login__form-avatar"><img :src="require('@/assets/img/' + login.avatar.img)"></div><h2 class="login__form-title" v-html="login.title"></h2><div class="login__form-input-row"><main-input v-for="item in login.input" :key="item.id" :elId="item.id" :elType="item.type" :elClass="item.class" :elValue="item.value" :elPlaceholder="item.placeholder" @inputAction="loginInputSave(item.id)"></main-input><main-btn :elText="login.btnText" elClass="login__form-btn" @clickAction="loginFirebase()"></main-btn></div><p class="login__form-text"><span>{{ login.btnRegister }}</span><button class="title-gradient" type="button" @click.once="logAnimShow(), animLogin(), registrationTo()">Registration...</button></p></form></div></div></section>